.contentWrap{
    position: absolute;
    top: 84px;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    min-height: 600px;
    overflow: hidden;
    .menuWrap{
        width: 200px;
        height: 100%;
        overflow: auto;
    }
    .content{
        flex: 1;
        display: flex;
        flex-direction: column;
        min-height: 600px;
        padding-left: 10px;
        padding-bottom: 10px;

        iframe{
            flex: 1;
            width: 100%;
            border: none;
            overflow: auto;
        }
    }
    .mobileContent{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        iframe{
            width: 375px;
            height: 667px;
            border-radius: 12px;
            box-shadow: #ebedf0 0 4px 12px;
        }
    }
}